import React, { useState, useEffect } from 'react'
import BlogForm from './components/BlogForm'
import Blogs from './components/Blogs'
import blogService from './services/blogs'
import Notification from './components/Notification'
import loginService from './services/login'
import Togglable from './components/Togglable'

const App = () => {
  const [ blogs, setBlogs ] = useState([])
  const [ newTitle, setNewTitle ] = useState('')
  const [ newAuthor, setNewAuthor ] = useState('')
  const [ newUrl, setNewUrl ] = useState('')
  const [ errorMessage, setErrorMessage ] = useState('')
  const [ errorType, setErrorType ] = useState('fail')
  const [ username, setUsername ] = useState('')
  const [ password, setPassword ] = useState('')
  const [ user, setUser ] = useState(null)
  const blogFormRef = React.createRef()

  useEffect(() => {
    blogService
      .getAll()
      .then(initialBlogs => {
        setBlogs(initialBlogs)
      })
  }, [])

  useEffect(() => {
    const loggedUserJSON = window.localStorage.getItem('loggedNoteappUser')
    if (loggedUserJSON) {
      const user = JSON.parse(loggedUserJSON)
      setUser(user)
      blogService.setToken(user.token)
    }
  }, [])

  const addBlog = async event => {
    event.preventDefault()
    blogFormRef.current.toggleVisibility()
    const blogObject = {
      title: newTitle,
      author: newAuthor,
      url: newUrl
    }
    try {
      const returnedBlog = await blogService.create(blogObject)
      setErrorType('success')
      setErrorMessage(
        `a new blog '${newTitle} by '${newAuthor}' added`
      )
      setTimeout(() => {
        setErrorMessage(null)
      }, 5000)
      setBlogs(blogs.concat(returnedBlog))
    }
    catch(error) {
      setErrorType('fail')
      setErrorMessage(error.response.data.error)
    }
    setNewTitle('')
    setNewAuthor('')
    setNewUrl('')
  }

  const handleTitleChange = (event) => {
    setNewTitle(event.target.value)
  }

  const handleAuthorChange = (event) => {
    setNewAuthor(event.target.value)
  }

  const handleUrlChange = (event) => {
    setNewUrl(event.target.value)
  }

  const handleLogout = () => {
    window.localStorage.clear()
    window.location.reload(false)
  }

  const handleLogin = async (event) => {
    event.preventDefault()
    try {
      const user = await loginService.login({
        username, password,
      })
      window.localStorage.setItem(
        'loggedNoteappUser', JSON.stringify(user)
      )
      blogService.setToken(user.token)
      setUser(user)
      setUsername('')
      setPassword('')
      setErrorType('success')
      setErrorMessage(
        'successful login'
      )
      setTimeout(() => {
        setErrorMessage(null)
      }, 5000)
    } catch (exception) {
      setErrorType('fail')
      setErrorMessage('wrong username or password')
      setTimeout(() => {
        setErrorMessage(null)
      }, 5000)
    }
  }

  const loginForm = () => (
    <form onSubmit={handleLogin}>
      <h1>log in to application</h1>
      <div>
        username
        <input
          type="text"
          value={username}
          name="Username"
          onChange={({ target }) => setUsername(target.value)}
        />
      </div>
      <div>
        password
        <input
          type="password"
          value={password}
          name="Password"
          onChange={({ target }) => setPassword(target.value)}
        />
      </div>
      <button type="submit">login</button>
    </form>
  )
  const blogForm = () => (
    <Togglable buttonLabel="new blog" ref={blogFormRef}>
      <BlogForm
        addBlog={addBlog}
        newTitle={newTitle}
        newAuthor={newAuthor}
        newUrl={newUrl}
        handleTitleChange={handleTitleChange}
        handleAuthorChange={handleAuthorChange}
        handleUrlChange={handleUrlChange}
      />
    </Togglable>
  )

  const loggedInHeader = () => (
    <div>
      <h2>blogs</h2>
      <p>{user.name} logged in <button type="submit" onClick={handleLogout}>logout</button></p>
    </div>
  )

  const updateLikesOf = async (id) => {
    try {
      const oldBlog = blogs.find(o => o.id === id)
      const changedBlog = { ...oldBlog, likes: oldBlog.likes+1 }
      await blogService.update(id, changedBlog)
      const newBlogs = await blogService.getAll()
      setBlogs(newBlogs)
      setErrorType('success')
      setErrorMessage(
        'thanks for like'
      )
      setTimeout(() => {
        setErrorMessage(null)
      }, 5000)
    }
    catch(error) {
      setErrorType('fail')
      setErrorMessage(
        'Cant add like'
      )
      setTimeout(() => {
        setErrorMessage(null)
      }, 5000)
      setBlogs(blogs.filter(n => n.id !== id))
    }
  }

  const deleteBlog = async (id) => {
    const blogToDelete = blogs.find(o => o.id === id)
    if (window.confirm(`Remove blog ${blogToDelete.title} by ${blogToDelete.author}?`)) {
      try {
        await blogService.deleteBlog(id)
        const newBlogs = await blogService.getAll()
        setBlogs(newBlogs)
        setErrorType('success')
        setErrorMessage(
          'blog successfully deleted'
        )
        setTimeout(() => {
          setErrorMessage(null)
        }, 5000)
      }
      catch(error) {
        setErrorType('fail')
        setErrorMessage(
          'Cant delete'
        )
        setTimeout(() => {
          setErrorMessage(null)
        }, 5000)
        setBlogs(blogs.filter(n => n.id !== id))
      }
    }
  }

  const allBlogs = () => (
    <div>
      <Blogs blogsToShow={blogs} updateLikesOf={updateLikesOf} deleteBlog={deleteBlog} name={user.name} />
    </div>
  )

  return (
    <div>
      <Notification message={errorMessage} type={errorType} />
      {user === null && loginForm()}
      {user !== null && loggedInHeader()}
      {user !== null && blogForm()}
      {user !== null && allBlogs()}
    </div>
  )
}

export default App
